<template>
  <div class="home">
    <div class="center" v-show="show">
      <h3 class="title-1">小姐姐姐，我观察你很久了</h3>
      <h3 class="title-2">做我的女朋友好不好?</h3>
      <img src="@/assets/love.gif" />
      <div class="btn">
        <mt-button type="primary" size="small" @click.prevent.stop="agreeClick">同意</mt-button>
        <mt-button type="danger" size="small" @click.prevent.stop="btnClick">不同意</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import { MessageBox } from "mint-ui";

export default {
  name: "home",
  components: {},
  data() {
    return {
      count: 0,
      count2: 0,
      show: true,
      messages: [
        "工资上交",
        "家务全包",
        "保大",
        "观点一致听我的",
        "不一致听你的",
        "我爱你",
        "答应我吧"
      ]
    };
  },
  watch: {},
  methods: {
    btnClick() {
      this.count++;
      for (let i = 0; i < this.messages.length; i++) {
        if (this.count === i + 1) {
          this.showMessage(this.messages[i]);
          if (this.count === this.messages.length) this.count = 0;
        }
      }
    },
    agreeClick() {
      this.count2++;
      if (this.count2 === 1) {
        MessageBox.alert("真的吗？你答应我了？", "提示").then(() => {
          MessageBox.alert("给我发消息吧，爱你", "提示").then(() => {
            this.count = 0;
            this.count2 = 0;
            this.$router.push("/love");
          });
        });
      }
    },
    showMessage(message) {
      MessageBox({
        title: "提示",
        message: message
      });
    }
  },
  mounted() {}
};
</script>

